.order-button {
    background-color: #ffa500;
    color: white;
    border: none;
    padding: 10px 22px;
    border-radius: 25px;
}
.header{
    background: url('hero-bg.jpg') no-repeat center center/cover;
    min-height: 100vh;
    display: flex;
    align-items: center;
    color: white;
    padding: 2rem;
    box-sizing: border-box;
    z-index: -1;
}
.header-content{
    text-align: center;
    max-width: 100%;
    width: 600px;
    margin-left: 5%;
}
.slider-text{
    padding : 1rem;
    border-radius: 10px;
    margin-bottom: 4rem;
  
}
.slider-text h2{
    font-family: 'Brush Script MT';
    font-size: clamp(2.5rem, 8vw, 4.375rem);
    margin-bottom: 1rem;
}
.slider-text p{
    margin: 1rem 0;
    font-size: 1.2rem;
    line-height:1.5;
}
